.site-content {
    margin: 95px 0 0 0;
}

// CTA MODULES
.cta-module {
    background-attachment: fixed;
    background-position: center 0;
	background-repeat: no-repeat;
    background-size: cover;
    color: $lightest;
    text-align: center;

    a {
        color: $lightest;

        &:hover {
            color: $accent !important;
            background-color: $dkaccent;
        }
    }

    .cta-btn-block {

        &.accent-bg {

            .accent-text {
                color: $dkgray;
            }
        }
    }
}

.portfolio-cta-module {
    color: $lightest;

    .cta-text {
        text-align: center;
    }

    &.lightest-bg {
        color: $accent;
    }
}

.cta-btn-block {
    margin: 0 0 10px 0;
    padding: 25px;
}

.cta-snippet {
    margin: 0 0 20px 0;
}

// PAGE INTRO
.page-intro {
    color: $lightest;

    &.p50 {
        padding-bottom: 0 !important;
    }

    .intro-block {
        background-color: $dkaccent;
        border-radius: $radius;
        padding: 20px 10px;
    }

}

// RECENT WORK MODULE
.recent-work {

    .constrain {
        text-align: center;
    }

    .big-title {
        margin: 0 0 10px 0;
    }
}

.recent-work-item {
    border-bottom: 1px solid $accent;
    border-top: 1px solid $accent;
    position: relative;

    &:first-of-type {
        border-top: 0;
    }

    &:last-of-type {
        border-bottom: 0;
    }

    .overlay {
        background-color: rgba( 0, 0, 0, 0.6 );
        color: $lightest;
        display: block;
        height: 100%;
        left: 0;
        position: absolute;
        text-align: center;
        top: 0;
        transition: $strans;
        width: 100%;

        .client-info-container {
            left: 0;
            margin: 0 auto;
            max-width: 85%;
            position: absolute;
            right: 0;
            top: 50%;
            transform: translateY( -50% );
        }
    }

    h4 {
        font-weight: 700;
    }

    .client-sector {
        margin: 0 0 10px 0;
    }

    .project-info {
        line-height: 1.15em;
    }
}

// TESTIMONIAL MODULE
.testimonials {
    font-weight: 500;

    .testimonial-intro {
        border-bottom: 4px solid $accent;
        margin: 0 0 10px 0;
        padding: 0 0 10px 0;
    }

    h2 {
        color: $dkaccent;
    }

    .slick-dotted.slick-slider {
        margin-bottom: 50px;
    }

    .slick-dots {
        bottom: -45px;

        li {
            background-color: $dkgray;

            &.slick-active {
                background-color: $accent;
            }

            button {

                &:before {
                    content: '';
                }
            }
        }
    }

    .client-name {
        font-weight: 500;
        padding: 20px 0 0 0;
        text-align: right;
    }

    .client-business {
        color: $accent;
        font-style: italic;
        text-align: right;
    }
}

.testimonial-slide {
    outline: 0;
    box-shadow: none;

    &:focus {
        outline: 0;
        box-shadow: none;
    }
}

// HOMEPAGE
.home .slick-slide {

    img {
        width: 100%;
        height: auto;
    }
}

.slide-content {
    background-color: $dkaccent;
    color: $lightest;
    height: 290px;
    overflow: hidden;
    padding: 15px;

    .text {
        margin: 0 0 15px 0;
    }

    a {

        &:hover {
            background-color: $lightest;
            color: $dkaccent;
        }
    }
}

.page-template-homepage {
    
    .service-content-container {
        background: $lightest;
        border-radius: $radius;
        box-shadow: $lshadow;
        margin: 20px 0 0 0;
        padding: 25px 15px 20px 15px;
        text-align: center;

        h2 {
            color: $dkaccent;
        }

        img {
            height: 85px;
            margin: 0 auto 30px auto;
        }

        p {
            margin: 0 0 20px 0;
        }

        a:hover {
            background-color: $accent;
        }
    }
}

.intro-block {
    box-shadow: $lshadow;
    overflow: hidden;
}

