.industry-block {
    background-color: $dkaccent;
    border-radius: $radius;
    color: $lightest;
    padding: 20px 10px;
}

.industries-text {

    .text {
        margin: 0 0 10px 0;
    }
}

.industries-list {

    ul {

        li {
            padding: 0 0 0 20px;
            position: relative;

            &:before {
                background-color: $accent;
                content: '';
                display: block;
                height: 10px;
                left: 0;
                position: absolute;
                top: 8px;
                width: 10px;
            }
        }
    }
}

// PAGE BANNER
.page-banner {
    background-position: center center;
    background-size: cover;
    color: $lightest;
    padding: 25px 0 20px;
}

// CLIENT PAGE
.client-intro {
    
    .intro-text {
        margin: 0 0 20px 0;
    }

    h5 {
        font-size: 18px;
    }

    .intro-share {
        border-bottom: 2px solid $lightest;
        border-top: 2px solid $lightest;
        margin: 0 0 20px 0;
        padding: 10px 0;

        h5 {
            margin: 0;
        }

        a {
            color: $dkgray;
            margin: 0 15px 0 0;

            &:hover {

                > svg {
                    color: $lightest;
                }
            }
        }
    }

    .detail {
        border-bottom: 1px solid $lightest;
        margin: 0 0 5px 0;
        padding: 0 0 5px 0;
        overflow: hidden;
    }

    .client-website {
        margin: 20px 0 0;
    }
}

.project-details {
    margin: 0 0 10px 0;

    .detail {
        display: block;
        margin: 0 0 10px 0;
    }

    .detail-title {
        font-weight: 500;
        text-transform: uppercase;
    }
}

.client-additional {
    color: $lightest;
}

.top-images {

    .full-width {
        margin-bottom: 15px;

        img {
            width: 100%;
        }
    }

    .third-width {
        margin-bottom: 15px;
    }
}

.full-image {

    img {
        margin: 0 auto;
    }
}

// SERVICE PAGE
.page-intro {

    .intro-block {

        &.service-intro {
            background-color: $dkaccent;
            background-image: none;
        }
    }

    .service-intro {
        padding: 0;

        &.flexxed {
            flex-direction: column-reverse;
        }
    }

    .service-text {
        padding: 10px;
    }
}

.services {

    .service-content-container {

        h2 {
            color: $dkaccent;
        }
    }
}

.services {

    .service-item {
        border-bottom: 2px solid #d6e1e8;
        margin: 0 0 35px 0;
        padding: 0 0 35px 0;

        &:last-of-type {
            border: none;
            margin: 0;
            padding: 0;
        }

        &.flexxed {
            flex-direction: column-reverse;
        }

        .service-image {
            margin: 0 0 15px 0;

            img {
                height: 100px;
                object-fit: cover;
            }
        }
    }
}

.single-service {

    .service-intro {

        img {
            height: 150px;
            object-fit: cover;
        }

        .services-text {
            padding: 15px;
        }

        h1 {
            font-size: 28px;
        }
    }

    .service-content {

        img {
            box-shadow: 0 0 30px 30px #fff;
        }
    }
}

// CONTACT PAGE
.contact {

    .intro-block {

        h3 {
            margin: 0 0 5px 0;
        }
    }

    .intro-text {
        margin: 0 0 20px 0;
    }

    .c-block {
        margin: 0 0 20px 0;

        &:last-of-type {
            margin: 0;
        }

        a.social-icon {
            color: $accent;    
            font-size: 28px;
            margin: 0 15px 0 0;
            transition: $mtrans;

           svg {
            color: $lightest               
           }

           &:hover {
               color: $ltgray;
               transition: $mtrans;
           }
        }
    }

    .contact-item {
        padding: 0 0 0 15px;
        position: relative;

        &:before {
            color: $accent;
            content: '\f101';
            font-family: $fa;
            left: 0;
            position: absolute;
            top: 0;
        }
    }

    .contact-form {
        background-color: $dkgray;
        border-radius: $radius;
        color: $lightest;
        padding: 10px;
    }

    input, select {
        margin: 0 0 10px 0;
    }

    select {
        border-radius: 0;
        -webkit-appearance: none;
        -webkit-border-radius: 0px;
    }

    input[type=submit] {
        background-color: $accent;
        border-radius: $radius;
        color: $lightest;
        font-size: 18px;
        font-weight: 700;
        height: 50px;
        margin: 10px 0 0 0;
        text-transform: uppercase
    }
}

.gmap {
    height: 300px;
    overflow: hidden;
}

// BLOG INDEX
.blog {

    .entry-header {
        position: relative;
        z-index: 999;
    }

    article {

        &:nth-of-type( even ) {
        
            h2 a {
                color: $dkgray;
            }
        }

        &:nth-of-type( odd ) {
        
            .btn-lg {
                background-color: $dkgray;
            }
        }
    }

    .page-intro.p50 {
        padding-bottom: 25px !important;
    }
}

.blog-post {
    border-bottom: 2px solid #d6e1e8;
    margin: 0 0 35px 0;
    padding: 0 0 35px 0;

    &.flexxed {
        -webkit-box-orient: vertical;
        -webkit-box-direction: reverse;
            -ms-flex-direction: column-reverse;
                flex-direction: column-reverse;
    }

    h2 a {
        font-weight: 700;
    }

    .post-thumbnail {
        overflow: hidden;

        img {
            height: auto;
            margin: 0 0 10px 0;
            width: 100%;
        }
    }

    .btn-lg {
        float: right;
        margin: 10px 0 0 0;
    }
}

// BLOG POST
.single-post {

    .intro-block {

        a {
            color: $lightest !important;
        }
    }

    .meta {
        margin: 0 0 5px 0;

        &:last-of-type {
            margin: 0;
        }
    }

    .post-thumbnail {
        margin: 0 0 10px 0;
    }
}

ul.meta-cat-list {
    display: inline-block;
    list-style: none;
    margin: 0 0 0 5px;

    li {
        display: inline-block;

        &:after {
            content: ', ';
        }

        &:last-of-type:after {
            content: '';
        }
    }
}

.meta-info-title {
    font-weight: 500;
}

.sharethis {

    a {
        margin: 0 5px;
    }
}