@media only screen and ( min-width: 480px ) {

    .slide-content {
        height: 300px;

        h2 {
            font-size: 36px;
        }
    }

    .slick-next,
    .slick-prev {
        top: 22%;
    }

    // HOMEPAGE
    .intro-block {
        margin: -50px 0 0 0;
    }

    .page-template-homepage {

        .intro-block {
            margin: initial !important;
        }

        .service-content-container {
            min-height: 450px;
            position: relative;

            a {
                bottom: 20px;
                left: 50%;
                min-width: 180px;
                position: absolute;
                transform: translateX( -50% );
            }
        }
    }

    .services {

        .services-list {

            .service-item {

                h2 {
                    font-size: 20px;
                }

                img {
                    height: 150px;
                }
            }
        }
    } 

    // SERVICE PAGE
    .single-service {

        .page-intro {
            margin: 10px 0 0 0;
        }

        .service-intro {
    
            .services-text {
                padding: 20px;
            }
    
            h1 {
                font-size: 34px;
            }
        }
    }

    // CLIENTS PAGE
    .clients {

        .clients-list {

            .flexxed {
                flex-direction: row;
                flex-wrap: wrap;
                justify-content: space-between;
            }
        }

        .recent-work-item {
            box-shadow: $lshadow;
            margin: 0 0 10px 0;
            width: 49%;
        }
    }

    // BLOG POST
    .single-post,
    .single-client {
        
        .intro-block {
            margin: -40px 0 0 0;
        }
    }

    .single-post {

        .intro-block {
            margin: -40px 0 20px 0;
        }
    }
}

@media only screen and ( min-width: 568px ) {
 
    // CLASSES
    .big-title {
        font-size: 34px;
    }

    // HEADER
    .header-contact {
        margin: 0;
    }

    .header-connect {
        background-color: transparent;
        box-shadow: initial;
        padding: initial;
        position: initial;

        a#connect-open {
            display: none;
        }
    }

    .main-header {
        padding: 20px 0;
    }

    a.top-logo {
        width: 245px;
    }
    
    button#responsive-menu-button {
        top: 53px !important;
    }

    // GENERAL CONTENT
    .site-content {
        margin: 105px 0 0 0;
    }

    // RECENT WORK MODULE
    .recent-work {

        .flexxed {
            -webkit-box-orient: horizontal;
            -webkit-box-direction: normal;
                -ms-flex-direction: row;
                    flex-direction: row;
            -ms-flex-wrap: wrap;
                flex-wrap: wrap;
        }

        .big-title {
            margin: 0 0 15px 0;
        }
    }

    .recent-work-item {
        border: 1px solid $accent;
        margin: 0;
        width: 50%;

        .overlay {
            position: absolute;
            padding: 0;
        }
    }
    
    // HOMEPAGE
    .industries-list {

        ul {
            -webkit-column-count: 2;
                    column-count: 2;
            -webkit-column-gap: 40px;
                    column-gap: 40px;
        }
    }

    .page-template-homepage {

        .service-content-container {
            min-height: 490px;
        }
    }

    // PAGE BANNER
    .page-banner {
        padding: 35px 0 30px;
    }

    // SERVICES PAGE
    .services {

        .services-list {

            .service-item {

                img {
                    height: 175px;
                }
            }
        }
    }

    // FOOTER
    .top-footer {

        .flexxed {
            -webkit-box-orient: horizontal;
            -webkit-box-direction: normal;
                -ms-flex-direction: row;
                    flex-direction: row;
            -ms-flex-wrap: wrap;
                flex-wrap: wrap;
            -webkit-box-pack: justify;
                -ms-flex-pack: justify;
                    justify-content: space-between;
        }
    }

    .footer-col {
        width: 45%;
