        }

        .service-item {
            margin: 0 0 45px;
            padding: 0 0 45px;

            .service-image {
                box-shadow: 0 0 0 0 rgba(6,49,77,.75);
                transition: $mtrans;
            }

            &:hover {

                .service-image {
                    box-shadow: 10px 10px 0 0 rgba(6,49,77,.75);
                    transition: $mtrans;
                }
            }
        }
    }

    // SERVICE PAGE
    .single-service {

        .page-intro {
            margin: 20px 0 0;

            .services-text {
                padding: 30px;
            }
        }
    }

    // CONTACT PAGE
    .contact {

        .page-intro {

            .intro-text {
                width: 100%;
            }
        }
    }

    // BLOG INDEX
    .blog {

        .intro-block {

            &.flexxed {

                h1 {
                    width: 33%;
                }
            }
        }

        .page-intro.p50 {
            margin: 35px !important;
        }
    }

    // BLOG POST
    .single-post {

        .page-intro {

            .intro-block.flexxed {
                margin: -100px 0 30px 0;
            }
        }

        .post-thumbnail {
            margin: 0 0 30px 0;
        }
    }
}

@media only screen and ( min-width: 1100px ) {

    .p50 {
        padding: 50px 0;
    }

    .m35 {
        margin: 35px 0;
    }
    
    .mt35 {
        margin: 35px 0 0 0;
    }
    
    .mb35 {
        margin: 0 0 35px 0;
    }
    
    .m50 {
        margin: 50px 0;
    }
    
    .mt50 {
        margin: 50px 0 0 0;
    }
    
    .mb50 {
        margin: 0 0 50px 0;
    }

    // HEADER
    .main-navigation {

        li a {
            padding: 0 15px;
        }
    }

    // CTA-MODULE
    .cta-content {

        h3 {
