        }
    }

    .page-intro {

        .intro-block {
            margin: -135px 0 0 0;
        }
    }

    .services {

        .page-intro {

            .intro-block {
                margin: -120px 0 0;
            }
        }
    }

    // CLIENTS PAGE
    .clients {

        .recent-work-item {
            box-shadow: $lshadow;
            margin: 0 0 30px 0;
        }
    }

    // SERVICES PAGE
    .services {

        .services-list {

            .service-item {

                img {
                    height: 275px;
                }

                h2 {
                    font-size: 28px;
