@media only screen and ( min-width: 1200px ) {

    // CTA-MODULE
    .cta-content {
        width: 66%;

        h3 {
            font-size: 41px;
        }

        .text-block {
            font-size: 22px;
        }
    }

    .cta-btn-block {
        width: 33%;
    }

    .cta-snippet {
        font-size: 22px;
    }

    // PAGE INTRO
    .page-intro {

        .intro-block {
            margin: -140px 0 0 0;
            padding: 50px;
        }
    }

    // HOMEPAGE
    .home .slick-slide {

        .slide-text-wrapper {
            left: 60px;
        }
    }

    .page-template-homepage {

        .service-content-container {
            min-height: 500px;
        }
    }

    .industry-block {
        padding: 50px;
    }

    // PAGE BANNER
    .page-banner {
        padding: 45px 0 110px;
    }

    // SERVICE PAGE
    .single-service {

        .page-intro {
            margin: -130px 0 0;

            .intro-block {
                margin: 0;
                padding: 0;
            }
        }
    }

    // CLIENT PAGE
    .single-client {

        .page-intro {

            .intro-block {
                margin: -120px 0 0;
            }
        }
    }

    // CONTACT PAGE
    .contact {

        .contact-form {
            padding: 50px;
        }
    }

    // BLOG INDEX
    .blog-post {

        .post-thumbnail img {
            max-height: 350px;
        }
    }
}